<template>
	<view class='wrapper'>
		<view class="user_name">{{clientOrder.memberName}}</view>
		<view class="switch">
			<scroll-view
				:scroll-x="true" 
				:scroll-with-animation="true" 
				:enable-back-to-top="true"
				:scroll-into-view="domIndex"
			>
			<view class='list' :style="[{width:parentWidth *130+'px'}]">
				<view 
					:class="[idx==index?'active':'','item']" 
					v-for="(item,index) in clientOrder.orderList" 
					:key="index" 
					@click="changeSlide(item,index)"
					:id="'dom'+index"
				>
					<view class="want">意向车型</view>
					<view class="modal">{{item.carTypeName}}</view>
				</view>
			</view>
			</scroll-view>
		</view>
		<view class="cu-timeline">
			<view 
				:class="['cu-item',currentStep>=item.step?'text-blue':'no_pass',currentStep==item.step?'active':'']"
				 v-for="(item,index) in progressList"
				:key="index"
			 >
				<view class="look_car time_item">
					<view class="title">
						<text>{{item.name}}</text>
						<!-- <text class="status" v-if="currentOrderInfo.orderState==-1 && currentStep==item.step ">{{currentOrderInfo.orderStateName}}</text> -->
					</view>
					<!-- 普通节点 -->
					<block v-if="item.step<6">
						<view class="select_date" v-if="currentStep>=item.step">{{item.nodeDate}}</view>
					</block>
					<!-- 上传附件节点 -->
					<block v-else>
						<view class="select_date" v-if="currentStep>=item.step">{{item.nodeDate}}</view>
						<view class="upload_w" v-if='currentStep>=item.step'>
							<view class="upload">
								<view class="item" :key='idx' v-for="(lett,idx) in affixList">
									<view @click='lookAffix(lett,affixList)' class='inner_w'>
										<img :src="lett" class='pic'>
									</view>
								</view>
							</view>
						</view>
					</block>
				</view>
			</view>
		</view>
		
		
		
		<view class="cu-timeline">
			<!-- 上传附件  该节点都可以随时可以-->
			<view class="cu-item text-blue">
				<view class="look_car time_item">
					<view class="title flex-row-start">
						<text>员工上传推荐购车凭证</text>
						<text class="status" v-if="currentOrderInfo.orderState==-1">{{currentOrderInfo.orderStateName}}</text>
					</view>
					<view class="upload_w">
						<view class="upload">
							<view class="item" v-for='(litt,idx) in staffAffixList' :key='idx'>
								<!-- 如果原来有附件，不显示 -->
								<img src="../../static/delete.png" class='delete' @click='deleteFile(litt)' v-if="!hasStaffAffix">
								
								<view @click='staffLookPic(litt.openUrl,staffAffixList)' class='inner_w'>
									<image :src="litt.openUrl" class='pic'/>
								</view>
							</view>
							<!-- 如果原来有附件，或者 大于等于3 不显示 -->
							<view class="inner item" @click='takePicture' v-if="staffAffixList.length < 3 && !hasStaffAffix && currentOrderInfo.orderState!=-1">
								<image src="../../static/pic.png"></image>
							</view>
						</view>
						<view class="make_sure" @click="staffUpload"  v-if="!hasStaffAffix && currentOrderInfo.orderState!=-1">
							<button 
								:class='["cu-btn","block","text-qxblue",affixBtnDisabled?"disabled":""]' 
								:disabled="affixBtnDisabled"
							>
								确认上传
							</button>
						</view>
					</view>
				</view>
			</view>
			
			<!-- 评分 -->
			<view :class="['cu-item',currentOrderInfo.orderState>=15?'text-blue':'no_pass']">
				<view class="look_car time_item">
					<view class="title">订单评分</view>
					
					<view class="sd" v-if="rateScore>0">
						<view class="rate_w">
							<view class="rate_item">
								<Evaluate
									:size="36" 
									class="rate" 
									:margin="'0 15'" 
									:value="rateScore" 
									:text="rateText" 
									:disabled="true"
									:textColor="'#0096EA'"
									:star_fill="'/static/blue_start.png'"
								/>
							</view>
						</view>
					</view>
					<view class="upload_w" v-if="canRate  && currentOrderInfo.orderState!=-1 ">
						<view class="make_sure" @click="goRate">
							<button :class='["cu-btn","block","text-qxblue"]'>去评价</button>
						</view>
					</view>
				</view>
			</view>
		</view>
		
		<!-- 图片查看 -->
		<view class="cu-modal" :class="lookImg?'show':''" catchtouchmove="return">
			<view class="cu-dialog">
				<view class="bg-img">
					<image :src="currentImgUrl" class="dialogImg" mode="widthFix"></image>
				</view>
				<view class="cu-bar bg-white">
					<view class="action margin-0 flex-sub" @click="hideModal">关闭</view>
				</view>
			</view>
		</view>
		
		<!-- 评价 弹框-->
		<view class="cu-modal rate_dialog" :class="rateDialog?'show':''" catchtouchmove="return">
			<view class="cu-dialog padding">
				<view class="content text-left">
					<view class="title">订单评分</view>
					<view class="tip text-blue">需完成评价再确认上传</view>
					<view class="rate_w">
						<view class="rate_item">
							<Evaluate 
								@change="startChange" 
								:value="rateScore" 
								:size="36"
								:star_fill="'/static/blue_start.png'"
								class="rate"
								:textColor="'#0096EA'"
								:margin="'0 15'"
								:text="rateText"
							/>
						</view>
					</view>
				</view>
				<view class="action margin-0 flex-sub" @click="makeScore">
					<button class="cu-btn make_sure">确认评分</button>
				</view>
			</view>
		</view>
		
		<!-- 输入价格 -->
		<view class="cu-modal priceDialog" :class="priceDialog?'show':''" catchtouchmove="return">
			<view class="cu-dialog padding">
				<view class="content text-left">
					<view class="title">确认上传凭证</view>
					<view class="tip1 text-blue">请确认询价是否正确</view>
					<view class="flex-row-start ask-price">
						<view class="txt">询价</view>
						<input type="digit" placeholder="请输入询价情况" class="money flex-row-end" v-model="uploadInfo.inquiryInfo">
						<view class="txt">万元</view>
					</view>
				</view>
				<view class="action margin-0 flex-row-between">
					<button class='bu-btn cancel' @click="priceDialog=false">取消</button>
					<button class="cu-btn make_sure1" @click="uploadAffix">确认</button>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import request from "../../common/request.js"
	import {http2Https,getFileType,getDeleteId,formatTime,https2Http} from '../../common/tools.js'
	import Evaluate from '@/components/evaluate/rate.vue'
	export default {
		data() {
			return {
				idx:0,
				domIndex:'dom0',
				currentStep:0, //步数
				currentOrderId:"",
				progressList:[],// 节点进度
				clientOrder:{ // 用户所有订单
					memberName:'',
					orderList:[],
				},
				currentOrderInfo:{}, //当前订单
				affixList:[],
				parentWidth:0,
				lookImg:false,
				currentImgUrl:null,
				staffAffixList:[],//员工端上传凭证
				// staffAffixList_data:'',// 员工端时间
				affixBtnDisabled:true,//
				rateDialog:false ,//订单评分弹框
				rateScore:0, //订单评分
				canRate:false,//是否可评价
				rateText:"对党支部满意度",
				hasStaffAffix:true,// 是否上传完附件
				priceDialog:false,
				uploadInfo:{
					orderId:'',
					attFileUrls:'',
					inquiryInfo:''
				}
			};
		},
		onShareAppMessage(){
			return {
				title:'卫蓝购',//String	是	分享标题	
				path:'/pages/infomation/index',//String	是	页面 path ，必须是以 / 开头的完整路径。	QQ小程序不支持
				imageUrl:"/static/share.png"
			}
		},
		methods:{
			clearStatus(){
				this.rateScore=0; //订单评分
				this.canRate=false;//是否可评价
				this.affixList=[]
				this.staffAffixList=[]
			},
			hideModal(){
				this.lookImg=false
			},
			goRate(){
				this.rateDialog=true
			},
			startChange(e){
				this.rateScore=e.value
			},
			changeSlide(item,index){
				this.clearStatus()
				this.idx=index
				this.currentOrderInfo=this.clientOrder.orderList[this.idx]
				this.currentOrderId=item.orderId
				console.log("当前订单",this.currentOrderInfo)
				this.getStaffAffixAndScore()
				this.getOrderProgress(item.orderId)
				
			},
			deleteFile(item){
				this.request("/common/deleteFile",{fileId:item.fileId}).then((res)=>{
					if(res.resultCode==0){
						var _arr=this.staffAffixList
						for(let i=0;i<_arr.length;i++){ //删除 所有附件中的文件
							if(item.fileId==_arr[i].fileId){
								this.staffAffixList.splice(i,1)
								break
							}
						}
						if(this.staffAffixList.length<=0){
							this.affixBtnDisabled=true
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			staffUpload(){
				this.priceDialog=true
				this.uploadInfo.inquiryInfo=this.currentOrderInfo.inquiryInfo
			},
			uploadAffix(){
				let _picList=[]
				for(var i=0;i<this.staffAffixList.length;i++){
					_picList.push(this.staffAffixList[i].openUrl)
				}
				let _attFileUrls=String(_picList)
				this.uploadInfo.attFileUrls=_attFileUrls
				if(this.uploadInfo.inquiryInfo==null){
					delete this.uploadInfo.inquiryInfo
				}
				this.uploadInfo.orderId=this.currentOrderId
				this.request("/order/staff/addatts",this.uploadInfo).then((res)=>{
					if(res.resultCode==0){
						uni.showToast({title:res.resultMsg?res.resultMsg:'上传成功',icon:'none'})
						this.hasStaffAffix=true
						this.priceDialog=false
						this.clientOrder.orderList.forEach((item,index)=>{
							if(item.orderId==this.currentOrderId){ //维护附件状态
								item.staffAttUrls=_attFileUrls
							}
						})
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			
			takePicture(){
				var _that=this
				uni.chooseImage({
					count:1,
					sizeType:["compressed"],
					sourceType: ['album', 'camera'], //从相册选择
					success: function (res) {
						let imgPaths=res.tempFilePaths
						for(let i=0;i<imgPaths.length;i++){
							(function(i){
								uni.showLoading()
								uni.uploadFile({
									url:getApp().globalData.requestUrl+"/common/upload",
									filePath:imgPaths[i],
									name: 'upFile',
									header: {
										'content-type': 'multipart/form-data',
										'service-code':'QDMS',
										"ticket":JSON.parse(uni.getStorageSync('token')).value
									},
									success:(res)=>{
										const _data=JSON.parse(res.data)
										console.log(_data)
										let result=_data.data
										_that.staffAffixList.push({
											openUrl:http2Https(result.openUrl),
											fileId:result.fileId
										})
										_that.affixBtnDisabled=false
									},
									complete:(res)=>{
										uni.hideLoading()
									},
								})
							})(i)
						}
					}
				});
			},
			
			lookAffix(item,resouceList){ // item 为url 
				if(this.mobileModel=="VOG-AL00"){ //华为p30
					this.currentImgUrl=item;
					this.lookImg=true
				}else{
					uni.showLoading()
					uni.previewImage({
						current:item,
						urls:resouceList,
						longPressActions:{
							itemList: ['发送给朋友', '保存图片', '收藏']
						},
						fail(){
							this.currentImgUrl=item;
							this.lookImg=true
						},
						complete() {
							uni.hideLoading()
						}
					})
				}
			},
			
			staffLookPic(item,resouceList){
				var _pic=[]
				resouceList.forEach((item)=>{
					_pic.push(item.openUrl)
				})
				this.lookAffix(item,_pic)
			},
			getOrderProgress(orderId){
				this.request("/order/progress/show",{orderId:orderId}).then((res)=>{
					if(res.resultCode==0){
						let step=0
						// 判断节点处于第一步
						this.progressList=res.data
						this.progressList.forEach((item,index)=>{
							item.step=index+1; // 赋予步数
							if(item.nodeDate!=-1){
								item.nodeDate=formatTime(item.nodeDate,true)
								step++
							}
							if(item.attFileUrls){
								let _imgList=item.attFileUrls.split(',')
								this.affixList=[..._imgList]
							}
						})
						this.currentStep=step
						// 获取评分
						
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			makeScore(){
				if(!this.rateScore){ uni.showToast({title: `请对订单进行评价`,icon:'none'});return}
				this.request("/order/staff/evaluate",{orderId:this.currentOrderId,score1:this.rateScore}).then((res)=>{
					if(res.resultCode==0){
						this.rateDialog=false
						this.canRate=false
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			// 获取订单员工附件
			getStaffAffixAndScore(){
				if(this.currentOrderInfo.staffAttUrls){ //如果员工上传了附件
					this.hasStaffAffix=true
					var _picList=this.currentOrderInfo.staffAttUrls.split(",")
					_picList.forEach((item,index)=>{
						this.staffAffixList.push({
							openUrl:item,
							fileId:index
						})
					})
				}else{
					this.hasStaffAffix=false
				}
				
				if(this.currentOrderInfo.orderState>=15){
					this.canRate=true
				}
				// 获取评价订单 列表
				this.request("/order/evaluate/list",{orderId:this.currentOrderId}).then((res)=>{
					var _data=res.data
					if(res.resultCode==0){
						if(_data.length>0){ //如果已经评价过
							this.rateScore=_data[0].score
							this.canRate=false
						}
					}
				})
			}
		},
		components:{
			Evaluate
		},
		onLoad(params){
			this.clientOrder=JSON.parse(params.clientOrder) //当前用户所有订单信息
			this.clearStatus()
			if(params.currentOrderId){ // 员工 查看 订单
				this.currentOrderId=params.currentOrderId //当前订单
			}
			//遍历当前索引 使其swiper选中
			this.clientOrder.orderList.forEach((item,index)=>{
				if(item.orderId==this.currentOrderId){
					this.idx=index
					this.domIndex="dom"+this.idx
					this.currentOrderInfo=this.clientOrder.orderList[this.idx]
					this.getStaffAffixAndScore()
				}
			})
			// swiper 父元素宽度
			this.parentWidth=this.clientOrder.orderList.length
			
			// 获取当前订单进度
			this.getOrderProgress(this.currentOrderId)
			
		}
	}
</script>

