</style>
<style lang="stylus">
	.dialogImg{
		width:100%
	}
	.rate_dialog,.priceDialog
		.cu-dialog
			.title
				font-size:36upx;
				font-family:PingFangSC-Medium,PingFang SC;
				font-weight:500;
				color:rgba(31,35,52,1);
			.tip
				font-size:24upx;
				font-family:PingFangSC-Regular,PingFang SC;
				font-weight:400;
				margin-top:15upx
			.tip1
				margin:30upx 0upx 
			.rate_w
				.rate_item
					margin-top:50upx
					color:#1F2334
			.ask-price
				.txt
					font-size:28upx;
					font-weight:400;
					color:rgba(31,35,52,1);
				.money
					width:368upx;
					height:60upx;
					background:rgba(248,249,252,1);
					border-radius:8upx;
					font-size:32upx;
					font-weight:500;
					margin:0upx 10upx
					color:rgba(31,35,52,1);
		.make_sure
			height:88upx;
			line-height:88upx;
			background:rgba(31,35,52,1);
			border-radius:16upx;
			font-size:28upx;
			font-family:PingFangSC-Medium,PingFang SC;
			font-weight:500;
			color:rgba(255,255,255,1);
			width:100%
			margin-top:70upx
		.action
			margin-top:70upx
			.make_sure1
				height:88upx;
				line-height:88upx;
				background:rgba(31,35,52,1);
				border-radius:16upx;
				font-size:28upx;
				font-family:PingFangSC-Medium,PingFang SC;
				font-weight:500;
				color:rgba(255,255,255,1);
				flex:1
